import React from "react";
import Papa from "papaparse";

const CsvFileInput = ({ onFileLoad, handleNext }) => {
  const handleOnChange = (e) => {
    const file = e.target.files[0];
    e.preventDefault();

    if (file) {
      const reader = new FileReader();

      reader.onload = (event) => {
        // Create a TextDecoder instance for Windows-1252
        const arrayBuffer = event.target.result;
        const decoder = new TextDecoder("windows-1252");
        const decodedString = decoder.decode(arrayBuffer);

        // Parse the decoded UTF-8 string
        Papa.parse(decodedString, {
          delimiter: ";", // Adjust based on your CSV format
          header: true, // Assuming the first row is a header
          complete: (results) => {
            onFileLoad(results.data); // Pass the parsed data to the parent component
            handleNext();
          },
        });
      };

      // Read the file as an ArrayBuffer
      reader.readAsArrayBuffer(file);
    }
  };

  return (
    <div style={{ textAlign: "center" }}>
      <form>
        <input
          type="file"
          id="csvFileInput"
          accept=".csv"
          onChange={handleOnChange}
        />
      </form>
    </div>
  );
};

export default CsvFileInput;
