import { extractNumberAndText } from "./extractNumberAndText";
import { parseDate } from "./parseDate";

export const convertIntoRequiredFormat = (el) => {
  const date = el["Datum"] ? el["Datum"] : "";
  const { numberPart, textPart } = extractNumberAndText(el["Projekt"]);
  const activity =
    el["Aktivität"] === "Entwicklung" ? "Development" : el["Aktivität"];
  return {
    date: parseDate(date),
    data: [
      date,
      textPart ? textPart : numberPart,
      activity,
      el["Stunden"],
      el["Ticket"] + ": " + el["Kommentar"],
      textPart ? numberPart : "",
    ],
  };
};
