import { convertIntoRequiredFormat } from "./convertIntoRequiredFormat";
import ExcelJS from "exceljs";

export const mergeData = async (data) => {
  const workbook = new ExcelJS.Workbook();
  const worksheet = workbook.addWorksheet("MergedData");

  worksheet.getColumn(1).width = 10;
  worksheet.getColumn(2).width = 25;
  worksheet.getColumn(3).width = 15;
  worksheet.getColumn(4).width = 10;
  worksheet.getColumn(5).width = 50;
  worksheet.getColumn(6).width = 8;

  data
    .filter((el) => Object.values(el).some((value) => Boolean(value)))
    .map((el) => convertIntoRequiredFormat(el))
    .sort((a, b) => a.date - b.date)
    .forEach((item) => {
      if (item.data.some((elm) => elm)) {
        const row = worksheet.addRow(item.data);
        // Align first column's content to the right
        row.getCell(1).alignment = { horizontal: "right", vertical: "middle" };
        row.getCell(2).alignment = { horizontal: "left", vertical: "middle" };
        row.getCell(3).alignment = { horizontal: "left", vertical: "middle" };
        row.getCell(4).alignment = { horizontal: "right", vertical: "middle" };
        row.getCell(5).alignment = { horizontal: "left", vertical: "middle" };
        row.getCell(6).alignment = { horizontal: "right", vertical: "middle" };
      }
    });

  const buffer = await workbook.xlsx.writeBuffer();
  const blob = new Blob([buffer], {
    type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
  });
  const url = window.URL.createObjectURL(blob);

  const a = document.createElement("a");
  a.href = url;
  a.download = "updated_excel.xlsx";
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
};
