export const extractNumberAndText = (inputString) => {
  const parts = inputString.split(/-(.*)/s); // Split at the first hyphen
  const numberPart = parts[0].trim(); // The part before the hyphen is the number
  const textPart = parts[1] ? parts[1].trim().replace(/^\d+\s*/, "") : ""; // The part after the hyphen, removing leading digits and spaces
  return { numberPart, textPart };
};
